/* Styling the header */
.timeline-header{
    display:flex;
    justify-content: space-between;
    position:sticky;
    top: 0;
    background-color:#FFFFFF;
    height:50px;

}

/* Timeline header buttons styling */

.timeline-header-btn{
    background: transparent;
    border: 0px;
    color: #0866FF;
    
    padding: 0px 10px;
    
}   

.header-wrapper{
    display:flex;
    color:#0866FF;
    padding:15px;
    font-weight: 700;
    font-size: 24px;
}


/* styling post section */
.single-post-wrapper{
    height:700px;
    width:725px;
    /* border: 5px solid black; */
    text-align:left;
    display:flex;
    
    align-items:center;
    flex-direction:column;
    margin: 25px 0px;
    background-color: #FFFFFF;
    border-radius:10px;
}

.post-header-wrapper{
    margin:25px ;
    flex-direction:row;
    /* border: 5px solid black; */
    width: 700px;
}

.header-child-wrapper{
    display:flex;
    /* border: 5px solid blue; */
}



.post-title{
    /* border: 5px solid blue; */
}

.all-post-wrapper{
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction:column;
    background-color:#F2F3F5;
}

.post-content{
    /* background-color:lightcoral; */
    border: 2px solid #DDDFE2;
    width:725px;
    height: 400px;
    text-align:left;
    padding: 15px;
}

.post-buttons{
    width:700px;
    height: 50px;
    background-color: lightgoldenrodyellow;
    margin-top:20px;
}

.create-comment{
    width:700px;
    height: 25px;
    
    
}

/* .comments-div{
    background-color:blue;
    
} */

/* .likes-div{
    background-color: red;
} */

.components-wrapper{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    margin:25px;
    
    width: 100%;

}

.create-comment, .comments-div, .likes-div{
    margin-top: 10px;
    
}

.comments-div{
    width: 50%;
    
}

.likes-div{
    width: 50%;
    
}


/* delete post btn styling */
.delete-post-btn{
    color: black;
    background-color: #FFFFFF;
    border: 3px double #0866FF;
}

.delete-post-btn:hover{
    color:white;
    background-color:limegreen;
    border: 1px solid #DDDFE2;
}